// Center text
%centered {
 text-align: center;
}

// Hover colors


// border
%hidden {
  display: none;
}

// Clearfix
%clearfix {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

// margin 0 auto;
%margin-z-a {
  margin: 0 auto;
}


// Transitions

%transition-all {
  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  -o-transition: all .5s ease;
  -ms-transition: all .5s ease;
  transition: all .5s ease;
}
