main {

  .content-container {
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    .content {
      flex-basis: 70%;
      margin: 100px auto 0;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      flex-direction: column;
      hr {
       border: 0;
       height: 1px;
       background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
      }
      @media #{$brk-narrow} {
        flex-basis: 40%;
      }
      @media #{$brk-wide} {
        flex-basis: 25%;
      }
    }
    .content-icon {
      text-align: center;
      display: block;
      width: 200px;
      height: 90px;
    }
    @media #{$brk-narrow} {
      flex-direction: row;
    }
    @media #{$brk-wide} {
      margin: 0 auto;
      max-width: 1024px;
    }
  }
  .team {
    background: $silver;
    }
  }

.team-container,
.partners {
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  flex-direction: column;
  @media #{$brk-narrow} {
    flex-direction: row;
    justify-content: center;
  }
}

.partners {
  margin-left: auto;
  margin-right: auto;
  max-width: 1024px;
}

.testimonials {
  margin-top: 0;
  padding-top: 50px;
  background: $color-2;
}

.testimonials-slider {
  height: 350px;
}

.testimonial-1 {
  blockquote {
    margin: 50px auto !important;
  }
}

.slick-dots {
  bottom: 40px;
}

.slick-dots li button::before {
  font-size: 10px;
}

.partner {
  @extend %transition-all;
  flex-basis: 100%;
  height: 120px;
  opacity: .8;
  &:hover {
    opacity: 1;
  }
  @media #{$brk-narrow} {
    flex-basis: 50%;
  }
  @media #{$brk-wide} {
    flex-basis: 33%;

  }
}

.deliveries-number {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.map {
  margin: 50px auto;
  max-width: 1024px;
}

#map {
  margin: 20px auto;
  height: 350px;
  width: 90%;
}





.dpd {
  background: $color-4;

}

.fedex {
  background: $color-3;
}

.yodel {
  background: $color-2;
}

.hermes {
  background: $color-5;
}

.tnt {
  background: $color-4;
}

.apc {
  background: $color-3;
}


/**********************************
*******       Services    *********
**********************************/

.removal-advert {
  background: url('../images/removal-soon.jpg') no-repeat center;
  background-size: cover;
  height: 200px;
  width: 100%;
  @media #{$brk-narrow} {
    height: 500px;
  }
}

.deliveries-chart {

    width: 100%;
    height: 300px;
    background: linear-gradient(to bottom, rgba($color-2, 0.9), rgba($color-2, 0.9)), url("../images/services-bg.jpg");
    background: -moz-linear-gradient(top, rgba($color-2, 0.9), rgba($color-2, 0.9)), url("../images/services-bg.jpg");
    background: -o-linear-gradient(top, rgba($color-2, 0.9), rgba($color-2, 0.9)), url("../images/services-bg.jpg");
    background: -ms-linear-gradient(top, rgba($color-2, 0.9), rgba($color-2, 0.9)), url("../images/services-bg.jpg");
    background: -webkit-gradient(linear, left top, left bottom, from(rgba($color-2, 0.9)), to(rgba($color-2, 0.9))), url("../images/services-bg.jpg");
    background: -webkit-linear-gradient(top, rgba($color-2, 0.9), rgba($color-2, 0.9)), url("../images/services-bg.jpg");
    background-position: center;
    background-size: cover;
}

.services-cont {
  @include flexbox();
  @extend %transition-all;
  -webkit-flex-direction: row;
  flex-direction: row;
}

.grid1,
.grid2,
.grid3 {
  opacity: 0;
}

.services {
  .grid-full:not(:last-child) {
    border-bottom: 2px solid $color-2;
  }
}


/**********************************
*******       Team        *********
**********************************/

.team-container {
  @media #{$brk-narrow} {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
}


.member-left,
.member-right {
  @extend %transition-all;
  width: 90%;
  border: 1px solid transparentize($color-2, .7);
  background: $silver;
  margin: 20px auto;
  @media #{$brk-narrow} {
    flex-basis: 45%;
  }
  @media #{$brk-wide} {
    flex-basis: 31%;
  }
  &:hover {
    background: transparentize($color-2, .5);
  }
}

/**********************************
*******      Contacts     *********
**********************************/

.contacts-cont {
  margin: 0 auto;
  width: 90%;
  display: flex;
  flex-direction: column;
  #map {
    margin: 50px auto;
  }
}
