/*Slides*/
.grid-full {
  width: 90%;
  margin: 100px auto;
  @media #{$brk-narrow} {
    margin-top: 200px;
  }
  @media #{$brk-wide} {
    margin-top: 400px;
  }
}
.grid-third {
  width: 33%;

  @media #{$brk-narrow} {

  }
  @media #{$brk-wide} {

  }
}

.grid-half {
  width: 50%;

  @media #{$brk-narrow} {

  }
  @media #{$brk-wide} {

  }
}

.header-slides   {
  width: 100%;
  border: none;
  border-bottom: 5px solid transparent;
  -moz-border-image: -moz-linear-gradient(left, $color-1 0%, $color-2 100%);
  -webkit-border-image: -webkit-linear-gradient(left, $color-1 0%, $color-2 100%);
  border-image: linear-gradient(to right, $color-1 0%, $color-2 100%);
  border-image-slice: 1;

  margin: 0 auto;
  @media #{$brk-narrow} {

  }
}
.slide {
  width: 100%;
  height: 95vh;
  text-align: center;
  z-index: 0 !important;
}
.slide h2 {
  font-family: 'Arimo', 'Helvetica', sans-serif;
	font-weight: bold;
	font-size: 2em;
	text-transform: uppercase;
	color: $white;
	text-shadow: 1px 1px 1px $black;
  margin: 0;

}
.slide p {
  font-family: 'Titillium Web', sans-serif;
	font-weight: normal;
	font-size: 1.125em;
	color: $white;
	text-shadow: 0 0 1px $black;
  width: 60%;
  margin: 0 20% 40px;

}

.slide img {
  width: 100%;
  // margin: 100px 50% 100px 10%;
}

