nav {
  @include flexbox();
  clear: both;
  height: 60px;
  width: 100%;
  max-width: 1024px;
  -webkit-flex-direction: column;
  flex-direction: column;
  background: $white;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  z-index: 2;
}

.sticky-wrapper {
  width: 100%;
  z-index: 100;
  display: block;

}

.navbar {
  @include flexbox();
  margin: 0 auto;
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  max-width: 1024px;
  height: 60px;
  width: 100% !important;
  background: $white;
  border-bottom: 1px solid $color-2;
  text-align: right;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  @media #{$brk-narrow} {

  }
}

.menu-bar {
  position: absolute;
  right: 0;
  height: 60px;
  .closed {
    margin-top: 15px;
    @extend %transition-all;
    cursor: pointer;
    .menu-dash {
      height: 3px;
      width: 30px;
      margin: 7px 20px;
      background: $color-2;
      position: relative;
    }
    .dash1 {
      @extend %transition-all;
      transform: rotateZ(0deg);
    }
    .dash2 {
      @extend %transition-all;
      opacity: 1;
    }
    .dash3 {
      @extend %transition-all;
      transform: rotateZ(0deg);
    }
  }
  .opened {
    @extend %transition-all;
    margin-left: 10px;
    .dash1 {
      @extend %transition-all;
      transform: rotateZ(-135deg);

      top: 10px;
    }
    .dash2 {
      @extend %transition-all;
      opacity: 0;
    }
    .dash3 {
      @extend %transition-all;
      transform: rotateZ(135deg);
      bottom: 10px;
    }
  }
  @media #{$brk-narrow} {
    display: none;
  }
}
.menu {
  z-index: 1;
  display: none;
  -webkit-flex-direction: column;
  flex-direction: column;
  position: relative;
  top: 60px;
  width: 60%;
  height: 200px;
  clear: both;
  text-align: center;
  -webkit-align-items: stretch;
  align-items: stretch;
  -webkit-justify-content: center;
  justify-content: flex-start;
  box-shadow: -1px 1px 4px transparentize($black, .8);

  .menu-item {
    @include flexbox();
    -webkit-align-items: stretch;
    align-items: stretch;

    text-align: center;

    .menu-link {
      @extend %transition-all;
      padding: 17px 20px;
      flex-basis: 100%;
      text-transform: uppercase;
      text-align: center;
      text-decoration: none;
      background: $silver;
      color: $color-1;
      font-weight: lighter;
      font-size: 0.725em;

      border-bottom: 1px solid rgba(128,128,128,.2);
      @media #{$brk-narrow} {
        background: $white;
        color: $color-2;
        font-weight: normal;
        border-bottom: 1px solid $color-2;
        &:hover {
          color: $white;
          border-top: 1px solid $white !important;
        }
      }
      &:hover {
        @media #{$brk-narrow} {
      		background: $color-1 !important;
      		color: $white !important;
      		border-bottom: 1px solid $white;
      	}
      }
    }
    @media #{$brk-narrow} {
      background: transparent;
      border: none;


    }
  }
  @media #{$brk-narrow} {
    @include flexbox();
    position: static;
    margin: 0;
    height: 60px;
    -webkit-flex-direction: row;
    flex-direction: row;
    -webkit-justify-content: flex-end;
    justify-content: flex-end;
    top: 0;
    z-index: 2;
    box-shadow: none;
  }
  @media #{$brk-wide} {
    max-width: 1024px;
  }
}

.hero {
  margin: 0 auto ;
  width: 90%;
  padding-top: 30vh;
  max-width: 900px;
  hr {
   border: 0;
   height: 1px;
   background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  }
  @media #{$brk-narrow} {
    padding-top: 60vh;
  }
}



