// Web fonts


// Text

@mixin text($size, $l-height: null, $weight: null, $color: null) {
  font-size: $size;
  line-height: $l-height;
  font-weight: $weight;
  color: $color;
}

// Transitions

@mixin transition($attr) {
  -webkit-transition: ease-in-out, $attr .5s ease-in-out;
  -moz-transition: ease-in-out, $attr .5s ease-in-out;
  -o-transition: ease-in-out, $attr .5s ease-in-out;
  transition: ease-in-out, $attr .5s ease-in-out;
}

@mixin hover-color($color, $percent) {
  &:hover {
     color: darken($color, $percent);
  }
}


// Flexbox

@mixin flexbox() {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}
