/* Web Fonts -------------------- */
@import url(https://fonts.googleapis.com/css?family=Arimo:400,700);
@import url(https://fonts.googleapis.com/css?family=Titillium+Web:400,300,700);

.logo {
	position: relative;
	height: 50px;
	width: 92px;
	margin: 5px 0 5px 20px;
	display: inline-block;
	left: 0;
}

.active {
	border-bottom: 2px solid $color-2 !important;
	border-top: 2px solid $color-2 !important;
	@media #{$brk-narrow} {
		background: $color-1 !important;
		color: $white !important;
		border-bottom: 1px solid $white !important;
		border-top: 1px solid $white !important;
	}

}

.hero-title {
	font-family: 'Arimo', 'Helvetica', sans-serif;
	font-weight: bold;
	font-size: 2em;
	text-transform: uppercase;
	color: $white;
	text-shadow: 1px 1px 1px $black;
	@media #{$brk-narrow} {
		text-align: center;
	}
}

.hero-text {
	font-family: 'Titillium Web', sans-serif;
	font-weight: normal;
	font-size: 1.125em;
	color: $white;
	text-shadow: 0 0 1px $black;
	@media #{$brk-narrow} {
		text-align: center;
	}
}

.number {
	text-align: center;
	font-family: 'Titillium Web', sans-serif;
	font-size: 4em;
	font-weight: lighter;
	color: $color-1;
	background: -webkit-linear-gradient(left, $color-1, $color-2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
	margin-bottom: 0;
}

.partners .number {
	flex-basis: 100%;
}

.number-2 {
	font-size: 5em;
}

.number-timeout1 {
	text-shadow: 1px 1px 3px transparentize($black, .5);
	padding-top: 80px;
	font-size: 1.5em;
	color: $white;
	-webkit-text-fill-color: $white;
	@media #{$brk-narrow} {
		margin: 0 auto;
		font-size: 3em;

	}
}

.number-timeout2 {
	text-shadow: 1px 1px 3px transparentize($black, .5);
	font-size: 2.5em;
	padding-top: 40px;
	color: $white;
	-webkit-text-fill-color: $white;
	@media #{$brk-narrow} {
		font-size: 4em;
		margin: 0 auto;

	}
}

.number-timeout3 {
	text-shadow: 1px 1px 3px transparentize($black, .5);
	font-size: 2em;
	padding-top: 60px;
	color: $white;
	-webkit-text-fill-color: $white;
	@media #{$brk-narrow} {
		font-size: 3.5em;
		margin: 0 auto;

	}
}

.number-text {
	font-family: 'Titillium Web', sans-serif;
	margin: 0 5%;
	color: $black;
	font-size: 1.5em;
	text-align: center;
	width: 90%;
}

.number-text-timeout1 {
	text-shadow: 1px 1px 2px transparentize($black, .4);
	color: $white;
	font-size: 1em;
	@media #{$brk-narrow} {
		font-size: 1.75em;
	}
}

.number-text-timeout2 {
	text-shadow: 1px 1px 2px transparentize($black, .4);
	color: $white;
	font-size: 1em;
	@media #{$brk-narrow} {
		font-size: 2.25em;
	}
}

.number-text-timeout3 {
	text-shadow: 1px 1px 2px transparentize($black, .4);
	color: $white;
	font-size: 1em;
	@media #{$brk-narrow} {
		font-size: 2em;
	}
}

.number-text2 {
	padding-bottom: 50px;
}
.main-heading {
	font-family: 'Arimo', 'Helvetica', sans-serif;
	color: $dove-gray;
	text-align: center;
}



.content-text {
	font-family: 'Arimo', 'Helvetica', sans-serif;
	color: $dove-gray;
	width: 90%;
	font-weight: normal;
	text-align: center;
	margin-top: 0;

}



.test-user {
	border-radius: 50%;
	display: block;
	margin: 0 auto;
	width: 128px !important;
	height: 128px !important;
}

.partner-img {
	width: 150px;
	height: auto;
	margin: 27px auto;
}

.map {
	.number-text {
		font-size: 1.25em;
	}
}

.copyright {
	font-size: 1em;
	color: $color-1;
	text-shadow: 1px 1px 1px rgba(255,255,255,0.2);
}


/* Services -------------*/

.service-img {
	width: 100%;
	display: block;
}

.services-heading {
	text-align: left;
	padding-left: 20px;
}

.services-text {
	text-align: left;
	padding-left: 20px;
}

.services-tick-text {
	margin-left: 10%;
	text-align: left;
	display: inline-block;
	width: 90%;
}

.num {
  background-color: #2d3945;
  color: #fff;
  height: 20px;
  width: 20px;
  display: inline-block;
  font-size: 13px;
  margin-right: 4px;
  line-height: 20px;
  text-align: center;
  text-shadow: 0 1px 0 rgba(255,255,255,0.2);
  border-radius: 0;
}

.tick {
  background: url('../icons/tick.png');
	background-size: cover;
  height: 15px;
  width: 15px;
  display: inline-block;
  font-size: 13px;

  margin-right: 4px;
  line-height: 20px;
  text-align: center;
  text-shadow: 0 1px 0 rgba(255,255,255,0.2);
  border-radius: 0;
}

.arrow-down {
  background: url('../icons/arrow-down.png');
	background-size: cover;
  height: 50px;
  width: 50px;
  display: block;
  font-size: 13px;
  margin: 0 auto;
  line-height: 20px;
  text-align: center;
  text-shadow: 0 1px 0 rgba(255,255,255,0.2);
  border-radius: 0;
}

.advert-heading {
	font-family: 'Titillium Web', sans-serif;
	font-size: 1.5em;
	font-weight: normal;
	text-shadow: 1px 1px 1px $black;
	text-align: center;
	padding-top: 75px;
	margin: 0;
	@media #{$brk-narrow} {
		font-size: 3em;
		padding-top: 200px;
	}
}


/****************************
********   Team   ***********/

.team-container {
	.number {
		flex-basis: 100%;
	}
}

.member-img {
	@extend %hidden;
	width: 90%;
	margin: 5% auto;
	max-width: 250px;
}

.member-left {
	.main-heading {
		margin: 10% auto 0;
		width: 90%;
		text-align: center;
		@media #{$brk-narrow} {
			text-align: left;
		}
		@media #{$brk-wide} {
			text-align: center;
		}
	}
	.number-text {
		font-size: 1.125em;
		margin: 0 auto;
		text-align: center;
		@media #{$brk-narrow} {
			text-align: left;
		}
		@media #{$brk-wide} {
			text-align: center;
		}
	}
	.number-text-bio {
		display: none;
	}
	&:hover {
		cursor: pointer;
	}
}

.member-right {
	.main-heading {
		margin: 10% auto 0;
		width: 90%;
		text-align: center;
		@media #{$brk-narrow} {
			text-align: right;
		}
		@media #{$brk-wide} {
			text-align: center;
		}
	}
	.number-text {
		font-size: 1.125em;
		margin: 0 auto;
		text-align: center;
		@media #{$brk-narrow} {
			text-align: right;
		}
		@media #{$brk-wide} {
			text-align: center;
		}
	}
	.number-text-bio {
		display: none;
	}
	&:hover {
		cursor: pointer;
	}
}

/****************************
********   Contacts   *******/

.contacts-cont {
	.number-text {
		font-size: 1.25em;
		margin-bottom: 50px;
	}
	form {
		margin-top: 50px;
		text-align: center;
	}
}

label {
	font-family: 'Arimo', 'Helvetica', sans-serif;
	color: $black;

}

input[type="text"],
textarea {
	min-width: 300px;
	font-size: 1em;
	color: $black;
	border: none;
	border-bottom: 1px solid $color-2;
	padding: 10px;
	margin: 0px 5px 10px 0;
}

textarea {
	min-height: 150px;
}

input[placeholder],
textarea[placeholder] {
	font-size: 0.8em;
	&:focus {
		outline: 0;
	}
}
