footer {
	margin-top: 50px auto;
	width: 100%;
	background: $silver;
	border: none;
	border-top: 2px solid $color-2;
  
	.footer-logo {
		display: block;
		height: 100px;
		width: 184px;
		margin: 20px auto;
	}
}


.social-list {
	margin-left: 7px;
	margin-top: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}



a.social {
  display: inline-block;
  margin-right: 10px;
  opacity: 0.7;
  background-size: 30px 30px;
  color: $white;
  transition: all .5s;
  font-size: 1rem;
}

a.facebook:before {
  display: inline-block;
  content: url('../icons/fb-icon.png');

}

a.gplus:before {
  display: inline-block;
  content: url('../icons/gplus-icon.png');

}

a.linkedin:before {
  display: inline-block;
  content: url('../icons/linkedin-icon.png');

}

a.social:hover {
  opacity: 1;
}

.copyright {
	font-family: 'Titillium Web', sans-serif;
	text-align: center;
	margin: 0;
	padding: 30px 0;
}
