.hero-btn {
  margin: 0 auto;
	font-weight: bold;
	font-size: 1em;
	text-transform: uppercase;
	color: $white;
	text-shadow: 1px 1px 2px transparentize($black, 0.5);
  display: block;
  font-family: 'Arimo', sans-serif;
  letter-spacing: 3px;
  color: $white;
  width: 200px;
  background: $color-2;
  border: none;
  @media #{$brk-narrow} {
		margin: 0 auto;
	}
}
