// Descriptive color variables

$white          : #fff;
$black          : #000;
$light-grey     : #f1eeee;
$buttered-rum	  : #e7ad47;
$boston-blue	  : #66a8de;
$tundora	  	  : #8A211D;
$lilav  		    : #4f6af0;
$dove-gray		  : #353535;
$silver 		    : #f0f0f0;
$alto			      : #d8d8d8;
$alabaster		  : #fbfbfb;
$chateau-green	: #A6AD3C;

// Functional color variables

$color-1   : $buttered-rum;
$color-2   : $boston-blue;
$color-3   : $lilav;
$color-4   : #00579f;
$color-5   : #3599ff;

$color-text-white   : $white;
$color-text-black	  : $black;
$color-text-base    : $tundora;


$color-link-default : $white;
$color-link-visited : $white;

// Font stacks

$stack-sans-serif : Helvetica, Arial, sans-serif;
$stack-open-sans  : 'Open Sans', $stack-sans-serif;

// Font variables

$semibold	: 600;
$lighter	: 400;

// Media queries breakpoints
$brk-lg-phones   : '(min-width: 360px)';
$brk-narrow      : '(min-width: 768px)';
$brk-wide     	 : '(min-width: 1024px)';
$brk-full-width  : '(min-width: 1440px)';

// Media queries for phone resolutions

$brk-ph-360		: '(min-width: 360px)';
$brk-ph-375		: '(min-width: 375px)';
$brk-ph-412		: '(min-width: 412px)';
